import React, { PureComponent, Suspense } from "react";
import { componentWillAppendToBody } from "react-append-to-body";
import { connect } from 'react-redux';
import './scss/Badge.scss'
import { getDataNextSteps } from '../../../actions/themeActions'
import { isTagEmbed } from "../../../actions/api";
const SmallBranding = React.lazy(() => import('../Elements/Branding/SmallBranding'));
const ReviewHeader = React.lazy(() => import('../../ReviewHeader'));
const Filters = React.lazy(() => import('../../Components/Filters'));
const WidgetTheme = React.lazy(() => import('../WidgetTheme'));
const PopUpCard = React.lazy(() => import('./Elements/PopUpCard'));
const AverageRatingFixed = React.lazy(() => import('./Elements/AverageRatingFixed'));
const SeeMore = React.lazy(() => import("../Elements/SeeMore/SeeMore"));
const MyComponent = ({ children }) => {
  return children;
}
const AppendedView = componentWillAppendToBody(MyComponent);


class Badge extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isClickStoped: false,
      isPopupVisible: false,
      showFirstButton: true,
      toggleSticky: false,
    };
    this.interval = null;
  }
  /*********************************************/
  togglePopup = () => {
    this.setState((prevState) => ({
      isPopupVisible: !prevState.isPopupVisible,
      showFirstButton: !prevState.showFirstButton
    }));
  };

  toggleDivs = () => {
    this.setState((prevState) => ({
      toggleSticky: prevState.isClickStoped ? false : !prevState.toggleSticky
    }));
  };
  /************************************************/


  componentDidMount() {
    /* Start the interval to toggle the divs every 5 seconds */
    this.interval = setInterval(this.toggleDivs, 10000);
    let wallEditor = document.getElementById("BadgeThemeScroll")
    if (wallEditor) wallEditor.addEventListener("scroll", this.autoLoadEditorScrollData);

  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }
  autoLoadEditorScrollData = event => {
    const { wall, appendData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    if (wall.Personalization.autoScrollStatus === 1) {
      let wallEditor = document.getElementById("BadgeThemeScroll");
      if (wallEditor) {
        let documentHeight = document.body.scrollHeight;
        let scrollTop = wallEditor.scrollTop;
        let scrollHeight = wallEditor.scrollHeight;
        if (scrollTop + wallEditor.clientHeight >= scrollHeight - 10) {
          if (!(loaderData && loaderData.isShowMoreLoading)) {
            const timeStamp = Math.floor(Date.now() / 1000);

            // Call the function to load more data
            this.props.getDataNextSteps(
              wall.Wall.id,
              timeStamp,
              postCount,
              appendData.networkID,
              appendData.after,
              appendData.heightEvent
            );
          }
        }
      }
    }
  }

  render() {
    const { postData, completeDataObject, adjustWidth, wall, reviewFilters, languageSetting, appendData, appData, filter_tags, webFilters, isFreeAdsStatus } = this.props;
    const { isPopupVisible, showFirstButton, toggleSticky } = this.state;
    const headerPositionTop = wall.Banner && Object.keys(wall.Banner).length > 0 && ((wall.Banner.status === 1 && wall.Banner.banner_position === 1) || wall.BannerImage.status === 1) ? true : false;
    const headerPositionBottom = wall.Banner && Object.keys(wall.Banner).length > 0 && wall.Banner.status === 1 && wall.Banner.banner_position === 2 ? true : false;
    const isReviewBanner = wall.Banner && Object.keys(wall.Banner).length > 0 && wall.Banner.status === 1 && wall.Banner.bannerType == 6 ? true : false
    const web_Filter_Status = isTagEmbed ? wall.Personalization.filterStatus === 1 ? true : false : webFilters && webFilters.length > 1 ? true : false;
    <Suspense fallback={<></>}><Filters type={wall.Personalization.filter_type} webFilters={webFilters} filter_tags={filter_tags} appendData={postData.appendData} wall={wall} languageSetting={languageSetting} postData={postData} /></Suspense>


    const result = Object.keys(completeDataObject).map((key) => completeDataObject[key]);
    const cardData = result.sort(() => Math.random() - Math.random())
      .find(() => true);
    return <div className="tb_bt__container">

      <AppendedView>

        {<div className={`tb_bt_popup_wrapper tb_bt_popup_show`} style={{ display: !isPopupVisible ? 'none' : null }}>
          <div className="tb_bt_popup_close_btn" onClick={this.togglePopup}>
            <div className="tb_bt_popup_close_ico tb__icon tb-close-alt"></div>
          </div>
          <div className="tb_bt_head_title">What our customer say</div>
          <div className="tb_bt_post_wrap" id="BadgeThemeScroll">
            {isReviewBanner && headerPositionTop ? <Suspense fallback={<></>}><ReviewHeader reviewFilters={reviewFilters} wall={wall} appendData={appendData} languageSetting={languageSetting} /></Suspense> : null}
            {web_Filter_Status ? <Suspense fallback={<></>}><Filters type={wall.Personalization.filter_type} webFilters={webFilters} filter_tags={filter_tags} appendData={appendData} wall={wall} languageSetting={languageSetting} postData={postData} /></Suspense> : null}

            <Suspense fallback={<></>}>
              <WidgetTheme {...this.props} /></Suspense>
            {wall.Personalization.loadMoreStatus === 1 ? <Suspense fallback={<></>}><SeeMore languageSetting={languageSetting} wall={wall} appendData={appendData} postData={appData.postData} /></Suspense> : null}
            {isReviewBanner && headerPositionBottom ? <Suspense fallback={<></>}><ReviewHeader reviewFilters={reviewFilters} wall={wall} appendData={appendData} languageSetting={languageSetting} /></Suspense> : null}
          </div>
        </div>}
      </AppendedView>

      <div className="tb_bt_wrapper">
        {showFirstButton &&
          <div className="tb_bt_post_wrapper">

            {!toggleSticky ?
              <>
                <div className={`tb_bt_average_sticky ${!toggleSticky ? 'tb_bt_show' : ''}`}>
                  <Suspense fallback={<></>}>
                    <AverageRatingFixed ThemeRule={wall.ThemeRule} reviewFilters={reviewFilters} togglePopup={this.togglePopup} />
                  </Suspense>
                </div>

                {isFreeAdsStatus ? <Suspense fallback={<></>}><SmallBranding languageSetting={languageSetting} wallId={wall.Wall.id} themeID={wall.Personalization.widgetTheme} /> </Suspense> : null}
              </>
              : null}

            {postData && postData.length > 0 && toggleSticky &&
              <div className={`tb_bt_post_contain ${toggleSticky ? 'tb_bt_show' : ''}`}>
                <Suspense key={cardData.id} fallback={<></>}>
                  <PopUpCard
                    ownerId={wall.Wall.owner}
                    itemData={cardData}
                    key={cardData.id}
                    itemIndex={cardData.id}
                    adjustWidth={adjustWidth}
                    personalization={wall.Personalization}
                    ThemeRule={wall.ThemeRule}
                    wallID={wall.Wall.id}
                    toggleDivs={this.toggleDivs}
                  /></Suspense>
              </div>
            }

          </div>}
      </div>
    </div>
  }
}

const mapStateToProps = state => {
  return {
    appData: state.appData,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Badge);